    url('../fonts/icons.woff?vp2yt2') format('woff'),
    url('../fonts/icons.svg?vp2yt2#icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
